.menu-wrapper{
    z-index: 200;
    height: 100px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.36);
    position: absolute;
    display: flex;
    align-items: center;
    text-shadow: 0px 4px 6px rgba(0, 0, 0, 0.21);
}

.menu-logo-text{
    color: #fff;
    display: flex;
    align-items: center;
    font-size: 20px;
    font-weight: 600;
}

.menu-links-wrapper{
    display: flex;
    justify-content: flex-end;
}

.menu-links{
    display: flex;
    justify-content: space-around;
    a{
        transition: .2s all linear;   
        -webkit-transition: .2s all linear; 
        -moz-transition: .2s all linear;  
        -ms-transition: .2s all linear; 
        -o-transition: .2s all linear;
        text-decoration: none;
        color: #fff;
        cursor: pointer;
        &:hover{
            color: #ddd;
        }
    }
}

.scroll-to-top-wrapper{
    position: fixed;
    bottom: 80px;
    right: 50px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background: #000;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 15px;
    z-index: 10000;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    cursor: pointer;
    transition: .2s all linear;   
    -webkit-transition: .2s all linear; 
    -moz-transition: .2s all linear;  
    -ms-transition: .2s all linear; 
    -o-transition: .2s all linear;
    &:hover
    {
        background: #fff;
        box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.21);
        color: #000;
    }
}