.footer-wrapper{
    height: 300px;
    background: #282828;
    margin-top: 50px;
}

.footer-top{
    height: 50%;
    color: #fff;
}

.footer-page-title{
    font-weight: bold;
    font-size: 17px;
}

.footer-menu-items{
    a{
        color: #fff;
        text-decoration: none;
        cursor: pointer;
        text-shadow: 0px 4px 6px rgba(0, 0, 0, 0.21);
        transition: .2s all linear;
        -webkit-transition: .2s all linear;
        -moz-transition: .2s all linear;
        -ms-transition: .2s all linear;
        -o-transition: .2s all linear;
        font-size: 15px;
        &:hover{
            color: #ccc;
        }
    }
}

.footer-bottom{
    height: 50%;
    background: #343434;
}

.footer-bottom-content{
    border-top: 2px solid #fff;
    height: 60%;
}

.footer-bottom-arr{
    color: #fff;
    letter-spacing: -0.035em;
    font-weight: 300;
}

.footer-bottom-icons{
    svg{
        margin: 0 20px 0 20px;
        cursor: pointer;
    }
}