.section-one-wrapper{
    background-image: url('SpringFashion-55.JPG.JPG');
    display: flex;
    height: 100vh;
    margin: 0;
    padding: 0;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    color: #fff;
}
.gradient{
    background: linear-gradient(287.56deg, rgba(53, 85, 146, 0.82) 0%, rgba(146, 109, 53, 0.65) 72.4%, rgba(146, 59, 53, 0.65) 100%);
    z-index: 5;
    position: absolute;
    height: 100vh;
    width: 100%;
}
.section-one-header-wrapper{
    z-index: 10;
    align-self: center;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    // background: #fca;
    width: 600px;
    height: 250px;
}
.section-one-description{
    display: flex;
    justify-content: center;
    @media screen and (max-width: 768px) {
        
    }
}
.section-one-header-buttons{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-self: center;
    width: 80%;
}
.section-one-button, .section-one-button-models{
    font-size: 1.1rem;
    width: 222px;
    height: 56px;
    border-radius: 96px;
    background: rgba(255, 255, 255, 0.15);
    border: 2px solid #FFFFFF;
    color: #fff;
    transition: .1s all linear;
    &:hover{
        background: #fff;
        color: black;
    }

    @media screen and (max-width: 768px) {
        margin-top: 20px;
    }
}
.section-one-button-models{
    background: rgba(0, 0, 0, 0.15);
}