.section-two-wrapper{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.section-two-header-wrapper{
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    // background: #fba;
    color: #000;
    width: 500px;
    height: 100px;
}
.section-two-header-text{
    font-size: 1.3rem;
    font-weight: 500;
    letter-spacing: -0.035em;
}
.section-two-gallery-wrapper{
    // background: #fba;
    height: auto;
    margin-top: -100px;
}
.section-two-gallery-item{
    color: #fff;
    display: flex;
    position: relative;
    align-items: center;
    margin: 5px 5px 5px 5px;
    height: 200px;
    background: #000;
    background-size: cover;
    cursor: pointer;
    @media screen and (max-height: 800px){
        height: 150px;
    }
}
.section-two-gallery-item-info-wrapper{
    background: rgba(0, 0, 0, .2);
    align-self: flex-end;
    width: 100%;
    height: 100%;
}
.section-two-gallery-item-info-bottom{
    position: absolute;
    height: 30px;
    display: flex;
    align-items: center;
    font-size: 14px;
    bottom: 0;
    background: rgba(0, 0, 0, 0.49);
    width: 100%;
}
.section-two-gallery-item-info-bottom-name, .section-two-gallery-item-info-bottom-date{
    margin: 0 5px 0 5px;
}
.section-two-gallery-item-title-wrapper{
    position: absolute;
    height: 50px;
    // background: #fff;
}
.section-two-gallery-item-title-text{
    font-size: 25px;
    font-weight: bold;
}