.section-three-wrapper{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #eee;
}

.section-three-info-wrapper{
    background: #fff;
    height: 400px; 
    box-shadow: 0px 0px 24px rgba(0, 0, 0, 0.25);
}

.section-three-info-box{
    
}

.section-three-info-wrapper-left-wrapper{
    height: 400px;
    background: #282828;
    position: relative;
    @media screen and (max-width: 1200px) {
        height: 150px;
    }
}

.section-three-info-left-svg{
    position: absolute;
    top: -50px;
    left: 50%;
    transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -o-transform: translateX(-50%);

    @media screen and (max-width: 1200px) {
        display: none;
    }
}

.section-three-info-left-text{
    position: absolute;
    bottom: 50px;
    display: flex;
    justify-content: center;
    text-align: center;
    color: #fff;
    letter-spacing: -0,035em;
    font-size: 18px;
    text-shadow: 0px 4px 6px rgba(0, 0, 0, 0.24);

    @media screen and (max-width: 1200px) {
        font-size: 15px;
        bottom: 10px;
    }
}

.section-three-info-wrapper-icons-wrapper{
    height: 400px;
    display: flex;
    justify-content: center;
    align-items: center;
    @media screen and (max-width: 1200px) {
        height: 200px;
    }
}

.section-three-info-wrapper-icons{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.section-three-info-wrapper-icon-email, .section-three-info-wrapper-icon-instagram{
    cursor: pointer;
    transition: .2s all linear;   
    -webkit-transition: .2s all linear; 
    -moz-transition: .2s all linear;  
    -ms-transition: .2s all linear; 
    -o-transition: .2s all linear;
    &:hover{
        .section-three-info-wrapper-icon-text{
            text-decoration-thickness: 2px; 
        }
    }
    @media screen and (max-width: 1200px) {
        margin-bottom: 20px;
    }
}

.section-three-info-wrapper-icon-text{
    font-size: 15px;
    margin-top: 10px;
    font-weight: bold;
    text-decoration: underline;
    text-decoration-thickness: 3px;    
}