.section-four-wrapper{

}

.gallery-section-wrapper{
    height: 100vh;
    border-top: 1px solid #ddd;
    
    @media screen and (max-height: 1200px){
        border-top: none;
    }
}

.section-four-gallery-wrapper{
    height: 100%;
    height: 70%;
}


.section-four-header-wrapper{
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    text-align: left;
    align-items: flex-end;
}


.section-four-header-title{
    font-size: 25px;
    font-weight: 600;
}

.section-four-header-date{
    color: #888;
}

.gallery-photo{
    padding: 5px;
    img{
        width: 100%;
        height: 350px;
        object-fit: cover;
        object-position: top;
        cursor: pointer;
        @media screen and (max-height: 800px){
            height: 250px;
        }
        @media screen and (max-height: 1000px){
            height: 300px;
        }
    }
}

.fullscreen-image{
    position: fixed;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background: rgba(0, 0, 0, 0.7);
    z-index: 2000000;
    img{
        width: 40%;
        height: auto;
        object-fit: cover;
        object-position: top;
        box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.21);
        @media screen and (max-width: 768px) {
            width: 100%;
        }
    }
}

.fullscreen-close{
    position: fixed;
    top: 10px;
    right: 20px;
    z-index: 100;
    button{
        width: 50px;
        height: 50px;
        background: #000;
        border: none;
        border-radius: 50%;    
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
}
}